@tailwind base;
@tailwind components;
@tailwind utilities;

/* Chewy font */
@import url('https://fonts.googleapis.com/css2?family=Chewy&display=swap');

body {
    margin: 0;
    font-family: 'Chewy', cursive;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

@layer base {
    :root {
        /* Burning Sea Color System - Fire meets Water */
        --background: 0 0% 100%;
        --foreground: 220 15% 15%;
        
        /* Primary - Fiery Coral Sunset */
        --primary: 12 88% 62%;
        --primary-foreground: 0 0% 100%;
        --primary-glow: 15 92% 68%;
        
        /* Secondary - Ocean Turquoise */
        --secondary: 189 85% 52%;
        --secondary-foreground: 0 0% 100%;
        --secondary-light: 189 85% 65%;
        
        /* Accent - Sunset Gold */
        --accent: 45 100% 60%;
        --accent-foreground: 220 15% 15%;
        
        /* Kids Area - Playful Purple/Pink */
        --kids-primary: 310 70% 60%;
        --kids-secondary: 280 65% 65%;
        
        /* Adult Area - Energetic Electric */
        --adult-primary: 190 85% 52%;
        --adult-secondary: 12 88% 62%;
        
        /* Base Colors */
        --card: 0 0% 100%;
        --card-foreground: 220 15% 15%;
        --popover: 0 0% 100%;
        --popover-foreground: 220 15% 15%;
        --muted: 210 20% 96%;
        --muted-foreground: 220 10% 45%;
        --border: 210 15% 88%;
        --input: 210 15% 88%;
        --ring: 12 88% 62%;
        
        /* State Colors */
        --destructive: 0 84% 60%;
        --destructive-foreground: 0 0% 98%;
        --success: 142 76% 36%;
        --success-foreground: 0 0% 100%;
        
        /* Custom Gradients */
        --gradient-fire: linear-gradient(135deg, hsl(12 88% 62%) 0%, hsl(30 100% 60%) 50%, hsl(45 100% 60%) 100%);
        --gradient-water: linear-gradient(135deg, hsl(189 85% 52%) 0%, hsl(210 85% 58%) 50%, hsl(230 70% 65%) 100%);
        --gradient-sunset: linear-gradient(135deg, hsl(12 88% 62%) 0%, hsl(30 90% 58%) 35%, hsl(45 100% 60%) 70%, hsl(189 85% 52%) 100%);
        --gradient-hero: linear-gradient(135deg, hsl(12 88% 62% / 0.15) 0%, hsl(189 85% 52% / 0.15) 100%);
        --gradient-kids: linear-gradient(135deg, hsl(310 70% 60%) 0%, hsl(280 65% 65%) 50%, hsl(189 85% 65%) 100%);
        --gradient-adult: linear-gradient(135deg, hsl(190 85% 52%) 0%, hsl(12 88% 62%) 100%);
        
        /* Custom Shadows */
        --shadow-elegant: 0 10px 30px -10px hsl(12 88% 62% / 0.25);
        --shadow-glow-fire: 0 0 40px hsl(12 88% 62% / 0.3);
        --shadow-glow-water: 0 0 40px hsl(189 85% 52% / 0.3);
        --shadow-soft: 0 4px 20px hsl(220 15% 15% / 0.08);
        
        /* Animations */
        --transition-smooth: 0.3s cubic-bezier(0.4, 0, 0.2, 1);
        --transition-bounce: 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55);
        
        --radius: 0.75rem;
    }
    
    .dark {
        --background: 220 20% 8%;
        --foreground: 0 0% 98%;
        --card: 220 18% 12%;
        --card-foreground: 0 0% 98%;
        --popover: 220 18% 12%;
        --popover-foreground: 0 0% 98%;
        --primary: 12 88% 62%;
        --primary-foreground: 0 0% 100%;
        --secondary: 189 85% 52%;
        --secondary-foreground: 0 0% 100%;
        --muted: 220 15% 18%;
        --muted-foreground: 210 10% 65%;
        --accent: 45 100% 60%;
        --accent-foreground: 220 20% 8%;
        --border: 220 15% 20%;
        --input: 220 15% 20%;
        --ring: 12 88% 62%;
    }
}

@layer base {
    * { @apply border-border; }
    
    body { 
        @apply bg-background text-foreground; 
        font-family: 'Chewy', cursive;
    }
    
    /* Naslovi sa “fun & comic” stilom */
    h1 {
        font-family: 'Chewy', cursive;
        font-size: 3rem;
        font-weight: 700;
        margin-bottom: 1rem;
        color: hsl(var(--primary));
        text-shadow: 3px 3px 8px rgba(0,0,0,0.25);
    }

    h2 {
        font-family: 'Chewy', cursive;
        font-size: 2.5rem;
        font-weight: 600;
        margin-bottom: 0.8rem;
        color: hsl(var(--secondary));
        text-shadow: 2px 2px 6px rgba(0,0,0,0.2);
    }

    h3 {
        font-family: 'Chewy', cursive;
        font-size: 2rem;
        font-weight: 600;
        margin-bottom: 0.6rem;
        color: hsl(var(--accent));
        text-shadow: 2px 2px 4px rgba(0,0,0,0.15);
    }

    h4 {
        font-family: 'Chewy', cursive;
        font-size: 1.75rem;
        font-weight: 500;
        margin-bottom: 0.5rem;
        color: hsl(var(--kids-primary));
    }

    h5 {
        font-family: 'Chewy', cursive;
        font-size: 1.5rem;
        font-weight: 500;
        margin-bottom: 0.4rem;
        color: hsl(var(--kids-secondary));
    }

    h6 {
        font-family: 'Chewy', cursive;
        font-size: 1.25rem;
        font-weight: 500;
        margin-bottom: 0.3rem;
        color: hsl(var(--adult-primary));
    }
}

@layer components {
    .gradient-fire { background: var(--gradient-fire); }
    .gradient-water { background: var(--gradient-water); }
    .gradient-sunset { background: var(--gradient-sunset); }
    .gradient-hero { background: var(--gradient-hero); }
    
    .text-gradient-fire {
        background: var(--gradient-fire);
        -webkit-background-clip: text;
        background-clip: text;
        -webkit-text-fill-color: transparent;
    }
    
    .text-gradient-water {
        background: var(--gradient-water);
        -webkit-background-clip: text;
        background-clip: text;
        -webkit-text-fill-color: transparent;
    }
    
    .shadow-glow-fire { box-shadow: var(--shadow-glow-fire); }
    .shadow-glow-water { box-shadow: var(--shadow-glow-water); }
    .shadow-elegant { box-shadow: var(--shadow-elegant); }
}

@layer utilities {
    .transition-smooth { transition: var(--transition-smooth); }
    .transition-bounce { transition: var(--transition-bounce); }
}

/* Animacije i efekti */
@keyframes wave { 0%, 100% { transform: translateY(0); } 50% { transform: translateY(-10px); } }
@keyframes float { 0%, 100% { transform: translateY(0px) rotate(0deg); } 50% { transform: translateY(-20px) rotate(3deg); } }
@keyframes pulse-glow { 0%, 100% { opacity: 1; } 50% { opacity: 0.7; } }
@keyframes shimmer { 0% { background-position: -1000px 0; } 100% { background-position: 1000px 0; } }

.animate-wave { animation: wave 3s ease-in-out infinite; }
.animate-float { animation: float 6s ease-in-out infinite; }
.animate-pulse-glow { animation: pulse-glow 2s ease-in-out infinite; }

/* Glassmorphism */
.glass { background: rgba(255,255,255,0.1); backdrop-filter: blur(10px); -webkit-backdrop-filter: blur(10px); border: 1px solid rgba(255,255,255,0.2); }
.glass-dark { background: rgba(0,0,0,0.2); backdrop-filter: blur(10px); -webkit-backdrop-filter: blur(10px); border: 1px solid rgba(255,255,255,0.1); }

/* Scroll, perspective, hover, text shimmer i ostale animacije ostaju nepromijenjene */
.perspective-1000 { perspective: 1000px; }
.hide-scrollbar { -ms-overflow-style: none; scrollbar-width: none; }
.hide-scrollbar::-webkit-scrollbar { display: none; }
html { scroll-behavior: smooth; }

@keyframes gradient-x { 0%, 100% { background-position: 0% 50%; } 50% { background-position: 100% 50%; } }
@keyframes glow-pulse { 0%, 100% { box-shadow: 0 0 20px rgba(var(--primary), 0.3); } 50% { box-shadow: 0 0 60px rgba(var(--primary), 0.6); } }
@keyframes text-shimmer { 0% { background-position: -200% center; } 100% { background-position: 200% center; } }
@keyframes morph { 0%, 100% { border-radius: 60% 40% 30% 70% / 60% 30% 70% 40%; } 50% { border-radius: 30% 60% 70% 40% / 50% 60% 30% 60%; } }

.animate-gradient-x { animation: gradient-x 15s ease infinite; background-size: 200% 200%; }
.animate-glow-pulse { animation: glow-pulse 3s ease-in-out infinite; }
.animate-text-shimmer { background-size: 200% auto; animation: text-shimmer 3s linear infinite; }
.animate-morph { animation: morph 8s ease-in-out infinite; }

.glass-premium { background: rgba(255, 255, 255, 0.08); backdrop-filter: blur(20px); -webkit-backdrop-filter: blur(20px); border: 1px solid rgba(255, 255, 255, 0.15); box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1); }

.text-gradient-animated {
    background: linear-gradient(90deg, hsl(var(--primary)) 0%, hsl(var(--accent)) 25%, hsl(var(--secondary)) 50%, hsl(var(--accent)) 75%, hsl(var(--primary)) 100%);
    background-size: 200% auto;
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    animation: text-shimmer 4s linear infinite;
}

.card-lift { transition: transform 0.3s ease, box-shadow 0.3s ease; }
.card-lift:hover { transform: translateY(-8px) scale(1.02); box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15); }

@keyframes scroll-bounce { 0%, 100% { transform: translateY(0); } 50% { transform: translateY(10px); } }
@keyframes spin { from { transform: rotate(0deg); } to { transform: rotate(360deg); } }
.animate-scroll-bounce { animation: scroll-bounce 2s ease-in-out infinite; }
.animate-spin-slow { animation: spin 8s linear infinite; }

@layer base {
    [data-debug-wrapper="true"] {
        display: contents !important;
    }

    [data-debug-wrapper="true"] > * {
        margin-left: inherit; margin-right: inherit; margin-top: inherit; margin-bottom: inherit;
        padding-left: inherit; padding-right: inherit; padding-top: inherit; padding-bottom: inherit;
        column-gap: inherit; row-gap: inherit; gap: inherit;
        border-left-width: inherit; border-right-width: inherit; border-top-width: inherit; border-bottom-width: inherit;
        border-left-style: inherit; border-right-style: inherit; border-top-style: inherit; border-bottom-style: inherit;
        border-left-color: inherit; border-right-color: inherit; border-top-color: inherit; border-bottom-color: inherit;
    }
}
